.app {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

  .dashboard {
    padding: 20px; /* Default for 1326x520 */
  }
  
  @media (min-width: 1440px) {
    .dashboard {
      padding: 30px; /* Adjusted for 1440x900 */
    }
  }
  
  .dashboard-first-line {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  

  .dashboard-create-project:hover {
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.4);
    background-color: #3a52d0;
  }

  .dashboard-create-project:active {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  }

  .proj-dropdown-option {
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
    border-radius: 4px;
    border: 1px solid black;
    background-color: #d7dbf2;
    color: #333;
  }

  .fill {
    padding: 8px 120px;
  }

  
  @media (min-width: 1440px) {
    .dashboard-create-project {
      padding: 8px 10px; /* Adjusted for 1440x900 */
      font-size: 16px;
    }
  }
  
  .dashboard-title {
    font-size: 18px; /* Default for 1326x520 */
    font-weight: bold;
    text-align: center;
    cursor: default;
  }
  
  @media (min-width: 1400px) {
    .dashboard-title {
      font-size: 20px; /* Adjusted for 1440x900 */
    }
  }
  
  .progress-section {
    background: rgb(243, 246, 248);
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.3);
    padding: 15px; /* Default for 1326x520 */
    margin: 20px 0; /* Default for 1326x520 */
    border-radius: 10px;
  }
  
  .progress-section h2 {
    font-size: 18px; /* Default for 1326x520 */
    font-weight: bold;
    padding-bottom: 10px;
    cursor: default;
  }
  
  @media (min-width: 1440px) {
    .progress-section h2 {
      font-size: 20px; /* Adjusted for 1440x900 */
    }
  }
  
  .history-section {
    display: flex;
    justify-content: space-between;
    gap: 15px; /* Default for 1326x520 */
    cursor: default;
  }
  
  @media (min-width: 1440px) {
    .history-section {
      gap: 20px; /* Adjusted for 1440x900 */
    }
  }
  
  .img1 {
    margin-top: 8px; /* Default for 1326x520 */
    width: 60%;
  }
  
  @media (min-width: 1440px) {
    .img1 {
      margin-top: 10px; /* Adjusted for 1440x900 */
    }
  }
  
  .status-overview,
  .activity-feed {
    background-color: #dce6f1;
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.3);
    padding: 15px; /* Default for 1326x520 */
    border-radius: 10px;
    flex: 1;
  }
  
  @media (min-width: 1440px) {
    .status-overview,
    .activity-feed {
      padding: 20px; /* Adjusted for 1440x900 */
    }
  }
  
  .activity-feed {
    flex: 1;
    padding: 15px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.3);
    margin: 0 10px;
    max-height: 400px;
    overflow-y: auto;
  }
  
  .activity-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .activity-item {
    padding: 10px;
    background: #f8f9fa;
    border-radius: 4px;
    border-left: 3px solid #4c66ff;
  }
  
  .activity-text {
    font-size: 14px;
    color: #333;
  }
  
  .no-activity {
    text-align: center;
    padding: 20px;
    color: #666;
    font-style: italic;
  }

  .dashboard-first-line {
  display: flex;
  align-items: center; /* Vertically aligns items */
  gap: 15px; /* Space between title and dropdown */
  margin-bottom: 15px;
}

.dashboard-title {
  font-size: 18px;
  font-weight: bold;
  margin: 0; /* Remove default margins */
  white-space: nowrap; /* Prevent title from wrapping */
}

.dashboard-create-project {
  color: white;
  padding: 5px 8px 5px 12px; /* Adjusted padding */
  font-size: 14px;
  border-radius: 8px;
  cursor: pointer;
  background-color: #4c66ff;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
  transition: all 0.3s ease-in-out;
  border: none;
  height: 30px;
  /* Custom dropdown arrow */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='white'%3e%3cpath d='M7 10l5 5 5-5z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-size: 12px;
  padding-right: 25px;
}