.app {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.board-main-content  {
  width: 90%; /* Default for 1326x520 */
  margin: 0 auto;
}

@media (min-width: 1440px) {
  .board-main-content {
    width: 85%; /* Adjusted for 1440x900 */
  }
}


.board-title{
  text-align: center;
    color: #333;
    font-size: 20px;
    margin-bottom: 15px; /* Default for 1326x520 */
    margin-top: 10px; /* Default for 1326x520 */
    cursor: default;
}

@media (min-width: 1440px) {
  .board-title {
    font-size: 22px;
    margin-bottom: 20px; /* Adjusted for 1440x900 */
    margin-top: 10px; /* Adjusted for 1440x900 */
  }
}

.board-controls {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

@media (min-width: 1440px) {
  .board-controls {
    margin-bottom: 30px;
  }
}

.board-controls input, .board-controls select, .board-controls button {
  padding: 3px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 13px;
}

@media (min-width: 1440px) {
  .board-controls input, .board-controls select, .board-controls button {
    padding: 8px;
    font-size: 18px;
  }
}

.board-task-board {
  display: flex;
  justify-content: space-between;
  gap: 20px;

  h3{
    margin-bottom: 20px;
    margin-top: 5px;
    font-size: 18px;
  }
}

.board-task-column {
  flex: 1;
  background: rgb(234, 231, 231);
  box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.5);
  padding: 15px;
  border-radius: 8px;
  min-height: 300px;
  max-height: 340px;
}

@media (min-width: 1440px) {
  .board-task-column {
    max-height: 750px;
    min-height: 400px;
  }
}

.board-task-container{
  overflow: auto;
  overflow-x: hidden;
  max-height: 260px;
}

@media (min-width: 1440px) {
  .board-task-container {
    max-height: 610px;
  }
}

.board-task-card {
  background: white;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s ease-in-out;
  cursor: grab;
  max-width: 400px;
  max-height: 180px;
  word-wrap: break-word;
  white-space: pre-wrap;
}

@media (min-width: 1440px) {
  .board-task-card {
    max-width: 600px;
    max-height: 220px;
  }
}

.board-task-card p {
  margin: 2px 0;
  font-size: 13px;
}

@media (min-width: 1440px) {
  .board-task-card p{
    margin: 2px 0;
  font-size: 15px;
  }
}

.board-task-card:hover {
  background-color: #7de9ff25;
  transition: transform 0.2s ease-in-out;
}

.task-actions {
  display: flex;
  gap: 8px;
  justify-content: end;
  margin-right: 5px;
}

/* Edit and Drop buttons */
.task-edit-button,
.task-drop-button {
  color: black;
  border: 1px solid black;
  padding: 4px 8px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 11px;
  display: flex;
  align-items: center;
  gap: 4px;
  transition: background 0.2s ease-in-out;
}

@media (min-width: 1440px) {
  .task-edit-button,
.task-drop-button {
  padding: 6px 10px;
  font-size: 13px;
  }
}

.task-edit-button.completed, .task-drop-button.completed, .task-edit-button.no, .task-drop-button.no {
  background-color: #f4f4f4;
  cursor: not-allowed;
}

.task-edit-button.completed:hover, .task-drop-button.completed:hover, .task-edit-button.no:hover, .task-drop-button.no:hover  {
  background-color: #f4f4f4;
}

.task-edit-button:hover {
  background-color: #4c67ff70;
}

.task-drop-button:hover {
  background-color: #4c67ff70;
}

.task-edit-popup-1{
  margin-bottom: 15px;
}

.task-popup-input{
  padding: 2px;
  display: flex;
  margin-bottom: 8px;
  width: 100%;
  }

  .task-save-btn-container{
    display: flex;
    justify-content: end;
    gap: 8px;
  }
  
  .task-save-btn{
  margin-top: 15px;
  font-weight: 700;
  }
  
  .task-save-btn:hover{
    border-radius: 3px;
    background-color: #065db35b;
    cursor: pointer;
  }

  .new-sprint-container {
    background: #f4f4f4;
      padding: 10px; /* Default for 1326x520 */
      border-radius: 8px;
      margin-bottom: 10px; /* Default for 1326x520 */
      cursor: default;
  }
  
  @media (min-width: 1440px) {
    .new-sprint-container {
      padding: 20px; /* Adjusted for 1440x900 */
        margin-bottom: 20px; /* Adjusted for 1440x900 */
    }
  }
  
  .new-sprint-title {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  @media (min-width: 1440px) {
    .new-sprint-title {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }
  
  .new-sprint-form-group {
    display: flex;
    gap: 8px;
  }
  
  @media (min-width: 1440px) {
    .new-sprint-form-group {
      gap: 10px;
    }
  }
  
  .new-sprint-input-field {
    flex: 1;
    padding: 6px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  @media (min-width: 1440px) {
    .new-sprint-input-field {
      padding: 10px;
    }
  }
  
  .new-sprint-create-button {
    background-color: #4c66ff;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-shadow: 1px 1px 2px black;
  }
  
  @media (min-width: 1440px) {
    .new-sprint-create-button {
      padding: 10px 15px;
    }
  }
  
  .new-sprint-create-button:hover {
    background-color: #003366;
  }

  .new-sprint-create-button.no{
    background-color: #4c66ff;
    cursor: not-allowed;
  }

  .new-sprint-create-button.no:hover{
    background-color: #4c66ff;
  }

.selected-popup-content-userstory h3 {
    font-size: 18px;
    margin-bottom: 15px;
    color: #333;
}

.popup-input-userstory {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
}

.popup-button-userstory {
    background-color: #4c66ff;
    color: white;
    border: none;
    padding: 8px 12px;
    margin-bottom: 30px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-shadow: 1px 1px 2px black;
}

.popup-button-userstory:hover {
    background-color: #003366;
}

.team-member-list-userstory {
    list-style: none;
    padding: 0;
    overflow-y: scroll;
    max-height: 250px; 
}

.team-member-list-userstory div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    padding: 8px;
    margin-top: 5px;
    border-radius: 4px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.team-member-list-userstory button {
    background: #c64a4a;
    color: white;
    border: none;
    padding: 5px 8px;
    border-radius: 4px;
    cursor: pointer;
}

.team-member-list-userstory button:hover {
    background: #db0000;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}