/* General page styling */
.auth-container {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: url('../scrum-background.jpg') no-repeat center center/cover;
    text-align: center;
  }

  /* Add a dark overlay */
.auth-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3); /* Adjust opacity (0 = fully transparent, 1 = fully black) */
  }
  
  .auth-logo-container{
    display: flex;
    align-items: center;
    padding: 30px;
  }

  .auth-logo{
    max-height: 60px;
    margin-left: 15px;
  }

  .auth-content {
    position: relative;
    z-index: 1;
    background: rgba(0, 0, 0, 0.7);
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    color: white;
    max-width: 600px;
  }
  
  .auth-content h1 {
    font-size: 28px;
  }
  
  .auth-content p {
    font-size: 16px;
    margin-bottom: 30px;
    letter-spacing: 0.5px;
  }
  
  /* Buttons */
  .auth-buttons {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  
  .auth-btn {
    flex: 1;
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
    transition: 0.3s;
  }
  
  .sign-in {
    background-color: #4c66ff;
    color: white;
    text-shadow: 1px 1px 2px black;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-size: 17px;
    border: 1px solid white;
  }
  
  .sign-in:hover {
    background-color: #3a52d0;
  }
  
  .sign-up {
    background-color: #003366;
    color: white;
    text-shadow: 1px 1px 2px black;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-size: 17px;
    border: 1px solid white;
  }
  
  .sign-up:hover {
    background-color: #0a5299;
  }